<template>
  <div
    class="wordbin-container"
    @mousedown="pushing = true"
    @mouseup="(draging = false), (pushing = false)"
    @mouseleave="(draging = false), (pushing = false)"
    @mousemove="handleMouseMove"
    @wheel="handleScroll"
    ref="container"
  >
    <n-button
      class="wordbin"
      v-for="(wordBin, i) in totalWordBins"
      :key="i"
      @mouseup="choose(i)"
      :type="choose_i == i ? 'success' : ''"
    >
      {{ wordBin.name }}
    </n-button>
  </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import { WordBin, default_wordbin } from "@/components/wordbin";

const emit = defineEmits(["update"]);

const container = ref(null);
const pushing = ref(false);
const draging = ref(false);
const handleMouseMove = (e) => {
  if (pushing.value) draging.value = true;
  if (draging.value) container.value.scrollLeft -= e.movementX;
};

const handleScroll = (e) => {
  container.value.scrollLeft += e.deltaY / 5;
};

const totalWordBins = ref([default_wordbin]);
const choose_i = ref(0);

onMounted(() => {
  emit("update", default_wordbin);
});

const choose = (i) => {
  if (draging.value) return;
  choose_i.value = i;
  emit("update", totalWordBins.value[i]);
};

const add_wordbin = (name, words) => {
  if (totalWordBins.value.find((_) => _.name == name)) {
    // [待完成]
    console.log("已存在同名词库");
  } else {
    totalWordBins.value.push(new WordBin(name, words));
  }
};

defineExpose({ add_wordbin });
</script>

<style scoped>
.wordbin-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  overflow: auto;
  gap: 10px;
}

.wordbin-container::-webkit-scrollbar {
  display: none;
}

.wordbin {
  width: 100px;
  height: 100px;
  font-size: 20px;
  white-space: normal;
  word-break: keep-all;
}
</style>


