<template>
  <a ref="file" href="" style="display: none" download />
  <n-button type="info" @click="download">导出</n-button>
</template>

<script setup>
import { ref } from "vue";

const file = ref(null);

const props = defineProps({ words: Array, name: String });

const download = () => {
  // 注意加上UNICODE标识
  let content =
    "\ufeff" +
    props.words
      .map((word) => `"${word.en}","${word.zh}","${word.tags.join(",")}",\n`)
      .join("");

  let blob = new Blob([content], { type: "text/csv,charset=UTF-8" });
  let src = URL.createObjectURL(blob);

  //设置下载路径
  file.value.download = props.name + ".csv";
  file.value.href = src;
  file.value.click();
};
</script>


