<template>
  <div class="left-menu">
    <div class="top">
      <WordBinSwitch ref="WordBinSwitchRef" @update="switch_wordbin" />
      <div class="file-btn-container">
        <UploadBtn @upload="upload" />
        <DownloadBtn :words="words" :name="name" />
      </div>
      <MultTagChoose :total="total_tags" @update="set_tags" />
    </div>
    <n-button type="error" @click="close()">关闭</n-button>
  </div>
</template>

<script setup>
import { ref } from "vue";
import UploadBtn from "@/components/UploadBtn.vue";
import DownloadBtn from "@/components/DownloadBtn.vue";
import WordBinSwitch from "@/components/WordBinSwitch.vue";
import MultTagChoose from "@/components/MultTagChoose.vue";

const props = defineProps({ close: Function });
const emit = defineEmits(["update:words"]);

const WordBinSwitchRef = ref(null);

const wordBin = ref(null);
const words = ref([]);
const total_tags = ref([]);
const tags = ref("");
const name = ref("");

const set_tags = (data) => {
  tags.value = data;
  words.value = data ? wordBin.value.select(data) : wordBin.value.words;
  emit("update:words", words.value);

  name.value = (wordBin.value.name + ` ${data}`).trim();
};

const switch_wordbin = (data) => {
  wordBin.value = data;
  total_tags.value = data.get_tags();
  set_tags("");
};

const upload = (name, words) => {
  WordBinSwitchRef.value.add_wordbin(name, words);
};
</script>

<style scoped>
.left-menu {
  width: calc(100% - 40px);
  height: calc(100% - 40px);
  padding: 20px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  gap: 20px;
  background-color: #fdfdfd;
}

.left-menu .top {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.file-btn-container {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
</style>
