<template>
  <n-card class="word-card" hoverable>
    <template #header>
      <div class="en" @click="word.en_show = !word.en_show">
        {{ en }}
      </div>
    </template>
    <template #default>
      <div class="zh" @click="word.zh_show = !word.zh_show">
        {{ zh }}
      </div>
    </template>
    <template #footer>
      <div class="tags">
        <n-tag v-for="(tag, i) in word.tags" :key="i">
          {{ tag }}
        </n-tag>
      </div>
    </template>
  </n-card>
</template>

<script setup>
import { computed } from "vue";

const props = defineProps({ word: Object });
const en = computed(() => {
  return props.word.en_show ? props.word.en : "";
});
const zh = computed(() => {
  return props.word.zh_show ? props.word.zh : "";
});
</script>

<style scoped>
.word-card {
  max-width: 480px;
  min-height: 200px;
  background-color: #f6f9fd;
}

.word-card .en,
.word-card .zh {
  min-height: 40px;
  line-height: 40px;
  width: 100%;
  font-size: 20px;
  cursor: pointer;
  user-select: none;
}

.word-card .en:hover,
.word-card .zh:hover {
  background-color: #e0e7f1;
}

.word-card .tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
</style>