<template>
  <div class="horizon-flow">
    <n-button
      v-for="(item, i) in items"
      :key="i"
      @click="toggle(item)"
      :type="item.on ? 'success' : ''"
    >
      {{ item.tag }}
    </n-button>

    <n-button @click="choose_all" :type="all_chosen ? 'info' : ''">
      全选
    </n-button>
    <n-button @click="clear_all" :type="none_chosen ? 'info' : ''">
      全不选
    </n-button>
  </div>
</template>

<script setup>
import { ref, watch } from "vue";
const props = defineProps({ total: Array });
const emit = defineEmits(["update"]);

const items = ref([]);

watch(
  () => props.total,
  (val) => {
    items.value = val.map((v) => {
      return {
        tag: v,
        on: false,
      };
    });
    all_chosen.value = false;
    none_chosen.value = true;
  }
);

const all_chosen = ref(false);
const none_chosen = ref(true);

const toggle = (item) => {
  item.on = !item.on;
  let choose = [];
  let all = true;
  items.value.forEach((t) => {
    if (t.on) choose.push(t.tag);
    else all = false;
  });
  if (all) {
    emit("update", props.total);
    all_chosen.value = true;
    none_chosen.value = false;
  } else if (choose.length) {
    emit("update", choose);
    all_chosen.value = false;
    none_chosen.value = false;
  } else {
    emit("update", "");
    all_chosen.value = false;
    none_chosen.value = true;
  }
};

const choose_all = () => {
  items.value.forEach((t) => (t.on = true));
  emit("update", props.total);
  all_chosen.value = true;
  none_chosen.value = false;
};

const clear_all = () => {
  items.value.forEach((t) => (t.on = false));
  emit("update", "");
  all_chosen.value = false;
  none_chosen.value = true;
};
</script>

<style scoped>
.horizon-flow {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}
</style>

