<template>
  <div class="top-menu">
    <n-pagination
      v-model:page="page"
      :page-count="pageCount"
      show-size-picker
      :page-sizes="pageSizes"
      v-model:page-size="pageSize"
    />

    <n-button
      type="success"
      :color="en_show ? '' : '#444'"
      @click="en_show = !en_show"
    >
      英文
    </n-button>

    <n-button
      type="success"
      :color="zh_show ? '' : '#444'"
      @click="zh_show = !zh_show"
    >
      中文
    </n-button>

    <n-button type="success" @click="backTop">
      <n-icon size="20">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          viewBox="0 0 24 24"
        >
          <g
            fill="none"
            stroke="currentColor"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path
              d="M9 12H5.414a1 1 0 0 1-.707-1.707l6.586-6.586a1 1 0 0 1 1.414 0l6.586 6.586A1 1 0 0 1 18.586 12H15v6H9v-6z"
            ></path>
            <path d="M9 21h6"></path>
          </g>
        </svg>
      </n-icon>
    </n-button>
  </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref, watch } from "vue";

const props = defineProps({
  words: Array,
  backTop: Function,
  open: Boolean,
});

const emit = defineEmits(["update"]);

const en_show = ref(true);
const zh_show = ref(true);

const page = ref(1);
const pageSize = ref(20);
const pageCount = ref(100);

onMounted(() => {
  window.addEventListener("wheel", handleWheel);
});

onUnmounted(() => {
  window.removeEventListener("wheel", handleWheel);
});

const handleWheel = (e) => {
  if (!props.open && pageSize.value == 5) {
    if (e.deltaY > 0) page.value = Math.min(pageCount.value, page.value + 1);
    else page.value = Math.max(1, page.value - 1);
  }
};

watch(
  () => props.words,
  () => {
    page.value = 1;
    update();
  }
);

watch(
  [
    () => page.value,
    () => pageSize.value,
    () => en_show.value,
    () => zh_show.value,
  ],
  () => {
    update();
  }
);

const pageSizes = [
  {
    label: "5 每页",
    value: 5,
  },
  {
    label: "10 每页",
    value: 10,
  },
  {
    label: "20 每页",
    value: 20,
  },
  {
    label: "50 每页",
    value: 50,
  },
];

const update = () => {
  let pn = page.value;
  let ps = pageSize.value;
  pageCount.value = Math.ceil(props.words.length / ps);

  let words = props.words.slice((pn - 1) * ps, pn * ps);
  let mirror = words.map((w) => {
    return {
      en: w.en,
      zh: w.zh,
      en_show: en_show.value,
      zh_show: zh_show.value,
      tags: w.tags,
    };
  });

  emit("update", mirror);
};
</script>

<style scoped>
.top-menu {
  width: calc(100% - 40px);
  padding: 20px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
</style>
