export class WordBin {
    constructor(name, words) {
        this.name = name;
        this.words = words;
    }
    get_tags() {
        let tags = [];
        this.words.forEach(word => {
            word.tags.forEach((tag) => {
                if (tags.indexOf(tag) < 0) tags.push(tag);
            });
        });
        tags.sort((a, b) => {
            let r1 = a.match(/\d+/);
            let r2 = b.match(/\d+/);
            if (r1 && !r2) return 100;
            if (r2 && !r1) return -100;
            return a - b;
        });
        return tags;
    }
    get_page_count(ps, tags) {
        if (Array.isArray(tags))
            return Math.ceil(
                this.words
                    .filter(w => {
                        for (let i = 0; i < tags.length; i++) {
                            if (w.tags.indexOf(tags[i]) < 0) return false;
                        }
                        return true;
                    })
                    .length / ps);
        else
            return Math.ceil(this.words.length / ps);
    }
    select(tags) {
        return this.words
            .filter(w => {
                for (let i = 0; i < tags.length; i++) {
                    if (w.tags.indexOf(tags[i]) < 0) return false;
                }
                return true;
            });
    }
}

import words from "@/components/default_wordbin";

export const default_wordbin = new WordBin("默认 词库", words);
