<template>
  <input
    ref="file"
    type="file"
    accept=".csv"
    style="display: none"
    @change="onChange"
  />
  <n-button type="info" @click="file.click()">导入</n-button>
</template>

<script setup>
import { ref } from "vue";

const file = ref(null);

const emit = defineEmits(["upload"]);

const onChange = (e) => {
  let file = e.target.files[0];

  // 及时清空，防止下一次无法上传同一地址的文件
  e.target.value = "";

  // 新建一个FileReader
  let reader = new FileReader();

  // 读取文件(以纯文本的形式返回文件内容)
  reader.readAsText(file, "UTF-8");

  reader.onload = (e) => {
    // // 文件内容字符串
    let file_string = e.target.result.replace("\r\n", "\n");
    let lines = file_string.split("\n").filter((e) => e);

    let words = lines
      .map((line) => {
        let ts = line
          .replace(/,+$/, "")
          .match(/".*?"/g)
          .map((e) => e.slice(1, -1));

        if (ts.length < 3) return;

        let en = ts[0].replace(/,+$/, "");
        let zh = ts[1].replace(/,+$/, "");
        let tags = ts[2].split(",").filter((e) => e);
        return { en, zh, tags };
      })
      .filter((e) => e);

    let name = file.name.replace(/\.csv/, "");
    emit("upload", name, words);
  };
};
</script>


