import { h, defineComponent } from "vue";

export default defineComponent({
    props: {
        column: {
            type: Number,
            default: 3
        }
    },
    render() {
        const { $slots, column } = this;
        if (!$slots.default) return h("div");
        return h("div", {
            style: {
                display: "grid",
                gridTemplateColumns: `repeat(${column},1fr)`,
                gap: "10px"
            }
        }, $slots.default()[0].children);
    }
});