<template>
  <div ref="slideFrameRef" class="slide-frame">
    <div :style="{ width: width + 'px' }">
      <slot name="left"></slot>
    </div>
    <div
      ref="triggerRef"
      class="trigger"
      @mouseenter="$emit('update:open', true)"
    ></div>
    <div ref="rightRef" class="right" @click="$emit('update:open', false)">
      <slot name="right" :backTop="backTop"></slot>
    </div>
  </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref } from "vue";

const props = defineProps({
  open: Boolean,
  width: {
    type: Number,
    default: 440,
  },
});
const emit = defineEmits(["update:open"]);

const slideFrameRef = ref(null);
const triggerRef = ref(null);
const rightRef = ref(null);
const back_top = ref(false);

const backTop = () => {
  back_top.value = true;
};

const update = () => {
  let t = slideFrameRef.value.scrollLeft;
  let tm = triggerRef.value.offsetLeft;

  let step = Math.floor((0.2 / tm) * t * (tm - t)) + 1;
  if (props.open) t = Math.max(0, t - step);
  else t = Math.min(tm, t + step);
  slideFrameRef.value.scrollLeft = t;

  if (back_top.value) {
    t = rightRef.value.scrollTop;
    t -= t / 10 + 1;
    t = Math.max(0, t);
    if (t == 0) back_top.value = false;
    rightRef.value.scrollTop = t;
  }
};

let timer;

onMounted(() => {
  slideFrameRef.value.scrollLeft = triggerRef.value.offsetLeft;
  timer = setInterval(update, 1);
});

onUnmounted(() => {
  clearInterval(timer);
});
</script>

<style scoped>
.slide-frame {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  overflow: auto;
}

.slide-frame::-webkit-scrollbar {
  display: none;
}

.slide-frame > * {
  height: 100%;
  flex-shrink: 0;
  overflow: auto;
}

.slide-frame .trigger {
  width: 4px;
  background-color: #de576d;
}

.slide-frame .right {
  width: calc(100% - 4px);
}
</style>


