<template>
  <SlideFrame v-model:open="open" :width="400">
    <template #left>
      <LeftMenu @update:words="set_words" :close="close" />
    </template>
    <template #right="{ backTop }">
      <div class="main-right">
        <div class="top">
          <TopMenu
            :words="words"
            :backTop="backTop"
            :open="open"
            @update="update_show_words"
          />
        </div>
        <VerticalFlow>
          <WordCard v-for="(word, i) in show_words" :key="i" :word="word" />
        </VerticalFlow>
      </div>
    </template>
  </SlideFrame>
</template>

<script setup>
import { ref } from "vue";
import SlideFrame from "@/components/SlideFrame.vue";
import LeftMenu from "@/components/LeftMenu.vue";
import TopMenu from "@/components/TopMenu.vue";
import WordCard from "@/components/WordCard.vue";
import VerticalFlow from "@/components/vertical_flow";

const words = ref(null);
const show_words = ref([]);
const open = ref(false);
const close = () => {
  open.value = false;
};

const set_words = (data) => {
  words.value = data;
};

const update_show_words = (data) => {
  show_words.value = data;
};
</script>

<style scoped>
.main-right {
  margin: 0 20px;
  width: calc(100% - 40px);
}

.main-right .top {
  width: 100%;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: #fff;
}
</style>


